footer {
  margin-top: 30px;
  padding: 15px;
  background: #222;
  color: white;
  text-align: center;
}

.footer-container {
  max-width: 1200px;
  margin: 0 auto;
}

/* Navigation Links */
.footer-links {
  display: flex;
  justify-content: center;
  gap: 20px;
  margin-bottom: 15px;
}

.footer-links a {
  color: #ffcc00; /* Yellow color */
  text-decoration: none;
  font-weight: bold;
}

.footer-links a:hover {
  text-decoration: underline;
}

/* Social Media Icons */
.social-media {
  display: flex;
  justify-content: center;
  gap: 15px;
  margin-bottom: 15px;
}

.social-media a {
  color: white;
  font-size: 24px;
  transition: transform 0.3s ease;
}

.social-media a:hover {
  transform: scale(1.2);
}

/* Copyright */
.copyright {
  font-size: 14px;
  margin-top: 10px;
  opacity: 0.8;
}
